import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmSettingsControl,
  VmControl,
  VmIcon,
  VmTooltip 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-settings-control', VmSettingsControl);
define('vm-control', VmControl);
define('vm-icon', VmIcon);
define('vm-tooltip', VmTooltip);
  

export interface SettingsControlProps extends JSX.VmSettingsControl, HTMLAttributes<HTMLVmSettingsControlElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmSettingsControlElement, SettingsControlProps>(
  'vm-settings-control',
  new Set(['icon','icons','tooltipPosition','tooltipDirection','menu','expanded','i18n']),
);
  